import React from 'react';
import { Link } from "gatsby"

import './project2.scss';
import './layout.scss';

const Project2 = props => (
    <div className="container-ultrawide">
        <div className="project2">
            <div>
                <a href={props.link} target="blank"> <img className="project2__img" src={props.image} alt=""/> </a>
            </div>
            <div className="project2__textarea">
                <a className="project2__title" href={props.link} target="blank"> {props.title} </a>
                <p className="project2__description"> {props.description} </p>
                <div className="project2__links">
                    <div>
                        {props.caseStudy ? <Link className="project2__link" to={props.caseStudy}> Case study </Link> : <div></div> }
                    </div>
                    <div>
                        {props.github ? <a className="project2__link project__link-git"  href={props.github} target="blank"> Code on GitHub </a> : <div></div> }
                    </div>
                </div>
            </div>
            
            {/* <a className="project__link"  href={props.github} target="blank"> Code on GitHub &rarr; </a>  */}
           
        </div> 
    </div>
)

export default Project2