import React from 'react';
import { Link } from "gatsby"

import './project3.scss';
import './layout.scss';

const Project3 = props => (
    <div className="wrapper-blue">
        <div className="container-ultrawide">
            <div className="project3">
                <div className="project3__textarea">
                    <a className="project3__title" href={props.link} target="blank"> {props.title} </a>
                    <p className="project3__description"> {props.description} </p>
                    <div className="project3__links">
                        <div>
                            {props.caseStudy ? <Link className="project3__link" to={props.caseStudy}> Case study </Link> : <div></div> }
                        </div>
                        <div>
                            {props.github ? <a className="project3__link project__link-git"  href={props.github} target="blank"> Code on GitHub </a> : <div></div> }
                        </div>
                    </div>
                </div>
                <div>
                    <a href={props.link} target="blank"> <img className="project3__img" src={props.image} alt=""/> </a>
                </div>
            </div> 
        </div>
    </div>
)

export default Project3